import { User } from './../model/user';
import { Stagiaire } from './../model/stagiaire';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'FiltreNomStg'
})
export class FilreNomStgPipe implements PipeTransform {

  private counter = 0;
  transform(stgs: User[], searchTerm: string): User[] {

    this.counter++;
    console.log('Filter pipe executed count ' + this.counter);

    if (!stgs || !searchTerm) {

      return stgs;
    }
    return stgs.filter(user => user.nom.toLowerCase().indexOf(searchTerm.toLowerCase()) !== -1);
  }

}
