import { Component, OnInit } from '@angular/core';
import { UploadFileService } from '../_services/UploadFile.service';
import { HttpResponse, HttpEventType } from '@angular/common/http';
import { observable, Observable } from 'rxjs';
import { UploadFile } from '../model/uploadFile';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  selectedFiles: FileList;
  currentFileUpload: File;
  nameFile: string;
  name: string;
  email: string;
  tel: string;
  description: string;
  progress: { percentage: number, } = { percentage: 0 };

  fileUploads: Observable<UploadFile[]>;

  // tslint:disable-next-line: variable-name
  constructor(private _uploadFileService: UploadFileService) { }

  ngOnInit() {
  }

  selectFile(event) {
    this.selectedFiles = event.target.files;
  }

  upload() {
    this.progress.percentage = 0;

    this.currentFileUpload = this.selectedFiles.item(0);
    // tslint:disable-next-line: max-line-length
    this._uploadFileService.pushFileToStorage(this.currentFileUpload, this.name, this.email, this.tel ,this.description).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded !');
      }
    });
    this.selectedFiles = undefined;
  }
}
