import { Component, OnInit } from '@angular/core';
import { Mission } from '../model/Mission';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { MissionService } from '../_services/mission.service';
import { TokenStorageService } from '../_services/token-storage.service';

@Component({
  selector: 'app-update-mission',
  templateUrl: './update-mission.component.html',
  styleUrls: ['./update-mission.component.css']
})
export class UpdateMissionComponent implements OnInit {
  currentUser: any;
  mission: Mission;
  idmission: number;

  missions: Observable<Mission[]>;

  // tslint:disable-next-line: max-line-length
  constructor(private token: TokenStorageService, private route: ActivatedRoute, private missionService: MissionService, private router: Router) { }

  ngOnInit() {

    this.currentUser = this.token.getUser();
    this.mission = new Mission();
    this.idmission = this.route.snapshot.params['idmission'];
    this.missionService.getMission(this.idmission)
      .subscribe(data => {
        console.log(data)
        this.mission = data;
      }, error => console.log(error));
  }

  editStagiaire() {
    this.missionService.updateMission(this.idmission, this.mission)
      .subscribe(data => console.log(data), error => console.log(error));
    this.mission = new Mission();
    this.gotoList();

  }

  onSubmit() {
    this.editStagiaire();
  }

  gotoList() {
    this.router.navigate(['liste-stagiares']);
  }


}
