
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class utilisateurService {

  private baseUrl = 'http://localhost:5000/api/v1/utilisateur';

  constructor(private http: HttpClient) { }

  getUtilisateur(idUser: number): Observable<any> {
    return this.http.get(`${this.baseUrl}/${idUser}`);
  }

  createUtilisateur(utilisateur: Object): Observable<Object> {
    return this.http.post(`${this.baseUrl}`, utilisateur);
  }

  updateUtilisateur(idUser: number, value: any): Observable<Object> {
    return this.http.put(`${this.baseUrl}/${idUser}`, value);
  }

  deleteUtilisateur(idUser: number): Observable<any> {
    return this.http.delete(`${this.baseUrl}/${idUser}`, { responseType: 'text' });
  }

  getUtilisateurList(): Observable<any> {
    return this.http.get(`${this.baseUrl}`);
  }

}
