import { Router } from '@angular/router';
import { Stagiaire } from './model/stagiaire';
import { Utilisateur } from './model/utilisateur';
import { Component, OnInit } from '@angular/core';
import { TokenStorageService } from './_services/token-storage.service';
import { Observable } from 'rxjs';
import { utilisateurService } from './_services/utilisateur.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  i: number;
  // tslint:disable-next-line: ban-types
  nbrU: number;
  nbrS: number;

  listestagiaires: Observable<Stagiaire[]>;
  utilisteurs: Observable<Utilisateur[]>;
  private roles: string[];
  isLoggedIn = false;
  showAdminBoard = false;
  showModeratorBoard = false;
  showUserBoard = false;
  nom: string;
  prenom: string;

  constructor(private tokenStorageService: TokenStorageService, private userService: utilisateurService, private router: Router) { }

  ngOnInit() {

    this.isLoggedIn = !!this.tokenStorageService.getToken();

    if (this.isLoggedIn) {
      const user = this.tokenStorageService.getUser();
      this.roles = user.roles;

      this.showAdminBoard = this.roles.includes('ROLE_ADMIN');
      this.showModeratorBoard = this.roles.includes('ROLE_MODERATOR');
      this.showUserBoard = this.roles.includes('ROLE_USER');
      this.nom = user.nom;
      this.prenom = user.prenom;
    }
  }

  logout() {
    this.tokenStorageService.signOut();
    window.location.reload();

  }
  getcount() {
    this.utilisteurs.subscribe(result => { this.nbrU = result.length; });
    this.listestagiaires.subscribe(result => { this.nbrS = result.length; });
  }

}
