import { Injectable } from '@angular/core';
import { HttpClient, HttpEvent, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UploadFileService {

  // tslint:disable-next-line: variable-name
  constructor(private _http: HttpClient) { }

  pushFileToStorage(file: File, name: string, email: string, tel: string, description: string): Observable<HttpEvent<{}>> {
    const formData: FormData = new FormData();
    formData.append('file', file);
    formData.append('name', name);
    formData.append('email', email);
    formData.append('tel', tel);
    formData.append('description', description);

    const req = new HttpRequest('POST', 'http://localhost:5000/api/v1/upload', formData, {
      reportProgress: true,
      responseType: 'text'
    });

    return this._http.request(req);
  }

  getFiles(): Observable<any> {
    return this._http.get('http://localhost:5000/api/v1/files');
  }


}
