import { EspaceStagiaireComponent } from './espace-stagiaire/espace-stagiaire.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { ProfileComponent } from './profile/profile.component';
import { BoardAdminComponent } from './board-admin/board-admin.component';
import { TestComponent } from './test/test.component';
import { BoardUserComponent } from './liste-utilisateurs/board-user.component';
import { AjoutUtilisateurComponent } from './ajout-utilisateur/ajout-utilisateur.component';
import { EspaceUtilisateurComponent } from './espace-utilisateur/espace-utilisateur.component';
import { UpdateUtilisateurComponent } from './update-utilisateur/update-utilisateur.component';
import { BoardModeratorComponent } from './liste-stagiaires/board-moderator.component';
import { AjoutStagiaireComponent } from './ajout-stagiaire/ajout-stagiaire.component';
import { UpdateStagiaireComponent } from './update-stagiaire/update-stagiaire.component';
import { ListeMissionsComponent } from './liste-missions/liste-missions.component';
import { AjoutMessionComponent } from './ajout-mession/ajout-mession.component';
import { UpdateMissionComponent } from './update-mission/update-mission.component';
import { ListFilesComponent } from './list-files/list-files.component';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'inscription', component: RegisterComponent },
  { path: 'profile', component: ProfileComponent },
  { path: 'liste-utilisateurs', component: BoardUserComponent },
  { path: 'liste-stagiaire', component: BoardModeratorComponent },
  { path: 'dashboard', component: BoardAdminComponent },
  { path: 'test', component: TestComponent },
  { path: 'add_utilisateur', component: AjoutUtilisateurComponent },
  { path: 'add_stagiaire', component: AjoutStagiaireComponent },
  { path: 'espace-utilisateur', component: EspaceUtilisateurComponent },
  { path: 'espace-stagiaire', component: EspaceStagiaireComponent },
  { path: 'update-utilisateur/:idUser', component: UpdateUtilisateurComponent },
  { path: 'update-stagiaire/:idStg', component: UpdateStagiaireComponent },
  { path: 'liste-missions', component: ListeMissionsComponent },
  { path: 'add_mission', component: AjoutMessionComponent },
  { path: 'update-mission/:idmission', component: UpdateMissionComponent },
  { path: 'CV', component: ListFilesComponent },

];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {useHash: true})
  ],
  exports: [
    RouterModule
  ],
  declarations: [

  ]
})
export class AppRoutingModule { }
