import { User } from './../model/user';
import { UserService } from './../_services/user.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StagiaireService } from '../_services/stagiaire.service';
import { Stagiaire } from '../model/stagiaire';
import { Observable } from 'rxjs';
import { TokenStorageService } from '../_services/token-storage.service';

@Component({
  selector: 'app-update-stagiaire',
  templateUrl: './update-stagiaire.component.html',
  styleUrls: ['./update-stagiaire.component.css']
})
export class UpdateStagiaireComponent implements OnInit {
  currentUser: any;
  user: User;
  idStg: number;
  listestagiaires: Observable<Stagiaire[]>;

  // tslint:disable-next-line: max-line-length
  constructor(private token: TokenStorageService, private route: ActivatedRoute, private userService: UserService, private router: Router) { }

  ngOnInit() {
    this.currentUser = this.token.getUser();
    this.user = new User();
    this.idStg = this.route.snapshot.params['idStg'];
    this.userService.getUser(this.idStg)
      .subscribe(data => {
        console.log(data)
        this.user = data;
      }, error => console.log(error));
  }

  editStagiaire() {
    this.userService.updateUser(this.idStg, this.user)
      .subscribe(data => console.log(data), error => console.log(error));
    this.user = new User();
    this.gotoList();
  }

  /*  showInfo() {
      this.toastr.info('This is an info toastr message!', 'Modifier');
    }*/

  onSubmit() {
    this.editStagiaire();
    //  this.showInfo();
  }

  gotoList() {
    this.router.navigate(['liste-stagiaire']);
  }
}
