import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MissionService {

  private baseUrl = 'http://localhost:5000/api/v1/Missions';

 constructor(private http: HttpClient) { }


 getMission(id: number): Observable<any> {
   return this.http.get(`${this.baseUrl}/${id}`);
 }

 // tslint:disable-next-line: ban-types
 createMission(stagiaire: Object): Observable<Object> {
   return this.http.post(`${this.baseUrl}`, stagiaire);
 }

 // tslint:disable-next-line: ban-types
 updateMission(id: number, value: any): Observable<Object> {
   return this.http.put(`${this.baseUrl}/${id}`, value);
 }

 deleteMission(id: number): Observable<any> {
   return this.http.delete(`${this.baseUrl}/${id}`, { responseType: 'text' });
 }

 getMissionList(): Observable<any> {
   return this.http.get(`${this.baseUrl}`);
 }

}
