import { Utilisateur } from './../model/utilisateur';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { utilisateurService } from '../_services/utilisateur.service';
import { Observable } from 'rxjs';
import { TokenStorageService } from '../_services/token-storage.service';
import { User } from '../model/user';

@Component({
  selector: 'app-update-utilisateur',
  templateUrl: './update-utilisateur.component.html',
  styleUrls: ['./update-utilisateur.component.css']
})
export class UpdateUtilisateurComponent implements OnInit {
  user: User;
  idUser: number;
  currentUser: any;
  listestagiaires: Observable<Utilisateur[]>;


  // tslint:disable-next-line: max-line-length
  constructor(private token: TokenStorageService, private route: ActivatedRoute, private userService: utilisateurService, private router: Router) { }

  ngOnInit() {
    this.currentUser = this.token.getUser();

    this.user = new User();
    this.idUser = this.route.snapshot.params.idUser;
    this.userService.getUtilisateur(this.idUser)
      .subscribe(data => {
        console.log(data);
        this.user = data;
      }, error => console.log(error));
  }

  reloadData() {
    this.listestagiaires = this.userService.getUtilisateurList();
  }

  editStagiaire() {
    this.userService.updateUtilisateur(this.idUser, this.user)
      .subscribe(data => console.log(data), error => console.log(error));
    this.user = new User();
    this.gotoList();

  }

  /*  showInfo() {
      this.toastr.info('This is an info toastr message!', 'Modifier');
    }*/


  onSubmit() {
    this.editStagiaire();
    // this.showInfo();
  }

  gotoList() {
    this.router.navigate(['liste-utilisateurs']);
  }


}
