import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { UploadFileService } from '../_services/UploadFile.service';
import { TokenStorageService } from '../_services/token-storage.service';

@Component({
  selector: 'app-list-files',
  templateUrl: './list-files.component.html',
  styleUrls: ['./list-files.component.css']
})
export class ListFilesComponent implements OnInit {

  currentUser: any;
  popoverTitle = 'Confirmation';
  popoverMessage = 'tes-vous sr de vouloir supprimer';
  confirmClicked = false;
  cancelClicked = false;
  fileUploads: Observable<string[]>;
  name = 'Angular';
  page = 1;
  pageSize = 6;
  nbr: number;


  constructor(private token: TokenStorageService, private _uploadFileService: UploadFileService) { }

  ngOnInit() {
    this.currentUser = this.token.getUser();
    this.fileUploads = this._uploadFileService.getFiles();
  }




}
