import { User } from './../model/user';
import { Utilisateur } from './../model/utilisateur';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { utilisateurService } from '../_services/utilisateur.service';
import { TokenStorageService } from '../_services/token-storage.service';
import { UserService } from '../_services/user.service';
import { AuthService } from '../_services/auth.service';

@Component({
  selector: 'app-ajout-utilisateur',
  templateUrl: './ajout-utilisateur.component.html',
  styleUrls: ['./ajout-utilisateur.component.css']
})
export class AjoutUtilisateurComponent implements OnInit {

  currentUser: any;
  user: User = new User();

  stagiaires: Observable<User[]>;

  // tslint:disable-next-line: max-line-length
  constructor(private token: TokenStorageService, private authService: AuthService, private serviceStg: UserService, private router: Router) { }

  ngOnInit() {
    this.currentUser = this.token.getUser();
    this.reloadData();
    this.user.rolee = 'utilisateur';
    this.user.role = ['mod'];
  }

  saveF() {
    this.authService.register(this.user)
      .subscribe(data => console.log(data), error => console.log(error));
    this.user = new User();
    this.router.navigate(['liste-utilisateurs']);

  }
  /*  showSuccess() {
      this.toastr.success('Vous avez bien ajout l"lment!', 'Succs');
    }*/

  reloadData() {
    this.stagiaires = this.serviceStg.getUserList();
  }


  onSubmitF() {
    this.saveF();
    //this.showSuccess();
  }

}
