import { UserService } from './../_services/user.service';
import { Stagiaire } from './../model/stagiaire';
import { MissionService } from './../_services/mission.service';
import { Mission } from './../model/Mission';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { TokenStorageService } from '../_services/token-storage.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ajout-mession',
  templateUrl: './ajout-mession.component.html',
  styleUrls: ['./ajout-mession.component.css']
})
export class AjoutMessionComponent implements OnInit {

  currentUser: any;
  mission: Mission = new Mission();
  missions: Observable<Mission[]>;
  stagiaire: Observable<Stagiaire[]>;

  // tslint:disable-next-line: max-line-length
  constructor(private token: TokenStorageService, private missionService: MissionService, private users: UserService, private router: Router) { }

  ngOnInit() {
    this.currentUser = this.token.getUser();
    this.reloadData();
  }

  saveF() {
    this.missionService.createMission(this.mission)
      .subscribe(data => console.log(data), error => console.log(error));
    this.mission = new Mission();
    this.router.navigate(['liste-missions']);

  }
  /*  showSuccess() {
      this.toastr.success('Vous avez bien ajout l"lment!', 'Succs');
    }*/

  reloadData() {
    this.missions = this.missionService.getMissionList();
  }

  onSubmitF() {
    this.saveF();
    //this.showSuccess();
  }
}
